// SignUpForm Component

import React, { useState, useEffect, useContext } from 'react'
import { Context } from "../../Context.js";
import { Amplify, Auth } from 'aws-amplify';

const SignUpForm = () => {
    
    // Global context to share states across components
//    const { isShowSignIn, setIsShowSignIn } = useContext(Context);
    
    const { SignIn, SignUp } = useContext(Context);
    const [ isShowSignUp, setIsShowSignUp ] = SignUp;

    // Local states in SignUpForm Component
    const [errorSignUp, setErrorSignUp] = useState('')
    const [isConfirmCodeHidden, setIsConfirmCodeHidden] = useState(true);
    const [prompt, setPrompt] = useState('')

    
    const [inputFields, setInputFields] = useState([
        {name: '', password: '', email: '', phone: '', confirmcode: ''}
    ])

    const handleFormChange = (index, event) => {
        let data = [...inputFields];
        data[index][event.target.name] = event.target.value;
        setInputFields(data);
        
    }


    
    async function signUp(username, password, email, phone) {
        
      try {
        const { user } = await Auth.signUp({
          username,
          password,
          attributes: {
            email,          // optional
//            phone,   // optional - E.164 number convention
            // other custom attributes
          },
          autoSignIn: { // optional - enables auto sign in after user is confirmed
            enabled: true,
          }
        });
        
        setIsConfirmCodeHidden(false);
        setIsShowSignUp(true);
        setErrorSignUp('');
        setPrompt('Check verfication code in email/phone');

        console.log(user);
      } catch (err) {
        console.log('Error signing up:', err);
        setErrorSignUp( <font color="red"> {err.message} </font> );
        setIsShowSignUp(true)
      }
    }


    async function confirmSignUp(username, code) {
      try {
        await Auth.confirmSignUp(username, code);
      } catch (err) {
        setErrorSignUp( <font color="red"> {err.message} </font> );
        console.log('error confirming sign up', err);
      }
    }
    


  return (
          
          
          <div className='fixed inset-0 z-10 overflow-y-auto'>
            <div className='flex items-end justify-center min-h-screen px-4 pt-4 pb-20 text-center sm:block sm:p-0'>
              
            
              <div className='fixed inset-0 transition-opacity' aria-hidden='true'>
                <div className='absolute inset-0 bg-gray-500 opacity-75' />
              </div>

              {/* }<!-- This element is to trick the browser into centering the modal contents. --> */}
              <span className='hidden sm:inline-block sm:align-middle sm:h-screen' aria-hidden='true'>&#8203;</span>
              
            
              <div className='inline-block overflow-hidden text-left align-bottom bg-white rounded-lg shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg sm:w-full' role='dialog' aria-modal='true' aria-labelledby='modal-headline'>
            
            
            <div className="login-form">
              <div className="form-box solid">
                <form>
            
            
            {inputFields.map((input, index) => {
               return (<div key={index}>
                  <input
                    type="text"
                    name='name'
                    placeholder='Name'
                    value={input.name}
                    onChange={event => handleFormChange(index, event)}

                  />
                 <br></br>

                  <input
                    type="password"
                    name='password'
                    placeholder='Password'
                    value={input.password}
                    onChange={event => handleFormChange(index, event)}

                  />
                   <br></br>

                   <input
                     type="email"
                     name='email'
                     placeholder='Email'
                     value={input.email}
                     onChange={event => handleFormChange(index, event)}

                   />
                   <br></br>

                   <input
                     type="phone"
                     name='phone'
                     placeholder='Phone'
                     value={input.phone}
                     onChange={event => handleFormChange(index, event)}

                   />
                   <br></br>
                       
                   <input
                     type="confirmcode"
                     name='confirmcode'
                     placeholder='confirmcode'
                     value={input.confirmcode}
                     onChange={event => handleFormChange(index, event)}
                     hidden={ isConfirmCodeHidden }

                   />
                   <br></br>



                </div>
              )
            })}
            
            
            
            
            <div className='px-4 py-3 bg-gray-50 sm:px-6 sm:flex sm:flex-row-reverse'>
    
             {/* Submit Sign Up*/}
              <button type='button' className='inline-flex justify-center w-full px-4 py-2 mt-3 text-base font-medium text-gray-700 bg-white border border-gray-300 rounded-md shadow-sm hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-teal-500 sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm' onClick={() => {
                  
                  if (isConfirmCodeHidden) {
                      signUp(inputFields[0].name, inputFields[0].password, inputFields[0].email, inputFields[0].phone);
                  }
                  else {
                      confirmSignUp(inputFields[0].name, inputFields[0].confirmcode);
                  }
                  
                  }}>
                 {isConfirmCodeHidden ? "Submit" : "Confirm" }
              </button>

            {/* Cancel Sign Up */}
            <button type='button' className='inline-flex justify-center w-full px-4 py-2 mt-3 text-base font-medium text-gray-700 bg-white border border-gray-300 rounded-md shadow-sm hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-teal-500 sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm' onClick={() => {
                setIsShowSignUp(false);
                setErrorSignUp('');
                }}>
              Cancel
            </button>
    
           {/* Prompt and Error */}
           {/* https://stackoverflow.com/questions/38521055/react-js-append-div-next-to-input-if-error  */}
           {prompt}
           {errorSignUp}
    

            </div>
    
        </form>
      </div>
    </div>
    
      </div>
    </div>
  </div>

  );
};

export default SignUpForm;
